<template>
    <h2>{{getProjectMeta('title')}}</h2>
    <nav class="project-page-menu">
        <li>
            <router-link 
                to="/projects/">
                <font-awesome-icon icon="arrow-left"/>
            </router-link>
            </li>
        <li 
            v-for="(value, name) in tabs"
            :key="name">
                <router-link 
                    :to="`/projects/${id}/${name}`">
                    {{value}}
                </router-link>
        </li>
    </nav>
    <div class="nest">
        <router-view></router-view>
    </div>
</template>

<style lang="scss">
    .project-page-menu{
        @include container(false);
        align-items: center;
        margin: 25px 0;
        width: min-content;
        max-width: 100%;
        li{
            list-style: none;
            a{
                display: block;
                padding: 25px;
                background: $bg-color-grey;
                color: $text-color-back;
                @include transition();
            }
            a:hover{
                box-shadow: $box-shadow-light;
            }
            .exact-active-link{
                pointer-events: none;
                color: $text-color-black;
                box-shadow: $box-shadow-strong;
                background: $bg-color-white;
            }
        }
    }
    .nest{
        @include container;
        padding: 40px;
    }
</style>

<script>

export default {
    name: 'Project Page',
    data(){
        return {
            id: this.$router.currentRoute._rawValue.params.id,
            tabs:
                {
                    journals: 'Журналы',
                    ed: 'ИД',
                    ks: 'КС',
                    settings: 'Настройки'
                }
        }
    },
    methods: {
        getProjectMeta(metaField){
            let arr = this.$store.state.projectsList;
            for (let idx of arr){
                if (idx.id === this.id){
                    if (metaField === 'title'){
                        return idx.title;
                    }
                }
            }
        }
    }
}
</script>